import React from 'react';
import Helmet from 'react-helmet';

import { ROOT_ROUTE, KONTACT_RUTA, O_NAMA_RUTA, GALERIJA_RUTA, KURSEVI_RUTA } from '../../helper/routes';

const HelmetHeader = (props) => {
  const {
    pageTitle = '',
    pageDescription = ''
  } = props;

  const pageSubTitles = () => {
    const { pathname } = window.location;
    let subtitlePage = '';
    let description = 'Sertifikovani kursevi stranih jezika u samom centru Grocke za sve nivoe i uzraste. Pripremni asovi za kolsko gradivo i meunarodne ispite. Kontakt: 065/47-93-883.';
    switch (pathname) {
    case ROOT_ROUTE:
      subtitlePage = 'kola stranih jezika';
      return { subtitlePage, description };
    case KONTACT_RUTA:
      subtitlePage = 'Kontakt';
      description = 'Tu smo za Vas. Na adresi, Vuka Milievia 7a, Grocka, nai ete nas radnim danima posle 15h. Ali, za sva Vaa pitanja tu je kontakt telefon 065/47-93-883.';
      return { subtitlePage, description };
    case O_NAMA_RUTA:
      subtitlePage = 'O nama';
      description = 'Naa kola stranih jezika nastala je iz elje da poboljamo kvalitet obrazovanja, i poznavanje odreenih stranih jezika naih polaznika uz najnovije metode i savremene programe.';
      return { subtitlePage, description };
    case GALERIJA_RUTA:
      subtitlePage = 'Galerija';
      description = 'Slika govori vie od hiljadu rei, kae mudra izreka. A mi Vam predlaemo da zavirite u nau galeriju i vidite kako mi to vodimo nae kurseve.';
      return { subtitlePage, description };
    case KURSEVI_RUTA:
      subtitlePage = 'Kursevi';
      description = 'U svojoj ponudi imamo kurseve svih nivoa, prilagoene svim uzrastima, vodeih svetskih jezika: engleskog, nemakog, italijanskog, francuskog i ruskog.';
      return { subtitlePage, description };
    default:
      return [{ subtitlePage, description }];
    }
  };

  const { subtitlePage, description } = pageSubTitles();
  const title = subtitlePage !== '' || pageTitle !== '' ? `Kontext Plus - ${subtitlePage || pageTitle}` : 'Kontext Plus - kola stranih jezika';

  return (
    <Helmet>
      <meta name='tags' content='engleski jezik, nemaki jezik, italijanski jezik, kursevi jezika, Grocka, Umari, Brestovik, Vrin, Bole, Letane, Vina, Kaluerica, pripremni asovi za osnovnu kolu, asovi engleskog za srednju kolu, asovi nemakog za srednju kolu, asovi engleskog za osnovnu i srednju kolu, kurs engleskog za vrti, engleski za najmlae, kursevi jezika za decu, kursevi jezika za odrasle, Kembrid ispiti, meunarodni sertifikati, CEA, FCE, KET, PET, Movers, Flyers, Starters, Goethe, nemaki za poetnike, konverzacijski kursevi, individualni kursevi jezika, testirajte svoje znanje jezika, sertifikovani testovi, sertifikovani kursevi, engleski A1, engleski A2 engleski B1, engleski B2, engleski C1, nemaki A1, nemaki A2, nemaki B1, nemaki B2, kola stranih jezika Grocka, prevod nemaki, prevod engleski, prevodilac engleskog, prevodilac nemakog, prevodilac italijanskog' />
      <meta name='keywords' content='Kontext plus, kola stranih jezika Grocka, privatni asovi engleskog, privatni asovi nemakog, kurs italijanskog, pripremni kursevi za meunaredne ispite, Priprema za Goethe, nemaki za poetnike, engleski za decu Grocka, Kontext Grocka, Kontext Plus Grocka, engleski za decu Vrin, engleski za decu Umari, engleski za decu Vina, kako brzo savladati engleski, kada je najbolje za dete da ui drugi jezik, engleska gramatika, rad u inostranstvu, kako dobiti sertifikat o znanju jezika, Groanska arija, jeftini kursevi jezika, popraviti govorni engleski, konverzacija na nemakom, nemaki jezik poetni nivo' />
      <meta name='description' content={description || pageDescription} />
      <title>{title}</title>
    </Helmet>
  );
};

export default HelmetHeader;
