import React from 'react';
import { Link } from 'react-router-dom';

import { O_NAMA_RUTA } from '../../helper/routes';

import SectionTwoImage from '../../images/home_school_photo-min.jpg';
import SectionThree from '../../images/home_school_picoftheweek-min.png';
import Imageone from '../../images/teaching-min.png';
import Imagetwo from '../../images/creativity-min.png';
import Imagethree from '../../images/reaction-min.png';

import './dashboard.scss';

const Dashboard = () => {
  return (
    <main className='container-fluid p-0'>
      <div className='container container-dash-custom'>
        <div className='row m-0'>
          <div className='col-md-12 p-0 mt-5 mb-5'>
            <div className='row'>
              <div className='col-md-4 mb-3'>
                <div className='first-section'>
                  <h2>Sta nas razlikuje?</h2>
                  <p className='m-0'>Naa misija je da obezbedimo da svako dete bude mentalno, emocionalno, drutveno i duhovno opremljeno za ivot.</p>
                  <ul className='mt-3'>
                    <li className='li-check'>Savremene metode poduavanja kao i moderni i ivopisni materijali</li>
                    <li className='li-check'>Kvalitet i profesionalnost</li>
                    <li className='li-check'>Pristupane cene i mnoge druge pogodnosti pri plaanju</li>
                  </ul>
                  <Link to={O_NAMA_RUTA} className='btn position-relative'>VIE</Link>
                </div>
              </div>
              <div className='col-md-4 mb-3'>
                <div className='second-section'>
                  <h2>Nastava</h2>
                  <img className='w-100' src={SectionTwoImage} alt='Nastava u naoj koli' />
                  <p className='m-0 mt-3'>Pored razvoja svih jezikih vetina, koncept nastave na naim kursevima pospeuje i razvoj drugih kognitivnih sposobnosti. Sva nastava je interaktivna, to
                    znai da podstie ne samo na usvajanje novih znanja lako i zanimljivo, ve i
                    reavanje zadataka u grupi i razvijanje socijalne inteligencije kod dece.
                  </p>
                </div>
              </div>
              <div className='col-md-4 mb-3'>
                <div className='thirt-section'>
                  <h2>Slika dana</h2>
                  <img src={SectionThree} className='w-100 h-100 mt-4 mb-4 pr-3 pl-3' alt='Naa slika dana' />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className='row mt-4 pr-3' style={{ color: 'black' }}>
          <div className='col-12 pr-xs-0 text-center mb-5 font-family-fensy' style={{ fontSize: '33px', fontWeight: 'bold' }}>
              Naa kola je <b className='pl-2 pr-2 pr-xs-0 pl-xs-0 our-school-is'>najbolji izbor</b> za Vas
          </div>
          <div className='col-12 pl-0 mb-5'>
            <div className='row'>
              <div className='col-md-4 border-right-custom'>
                <div className='row mb-4'>
                  <div className='col-5 text-center'>
                    <img src={Imageone} alt='asovi' style={{ width: '75%' }} />
                  </div>
                  <div className='col pl-0'>
                    <h4 style={{ fontWeight: 'bold' }}>asovi</h4>
                    <div className='font-size-15'>
                        Tradicionalni pristup nastavi nikada nije bio na nain rada. Usvajanje novog znanja ete najmanje svesno primetiti na naim kursevima. Videete samo da se dobro zabavljate!
                    </div>
                  </div>
                </div>
              </div>
              <div className='col-md-4 border-right-custom'>
                <div className='row mb-4'>
                  <div className='col-5'>
                    <img src={Imagetwo} alt='Kreativnost' className='w-100' />
                  </div>
                  <div className='col pl-0'>
                    <h4 style={{ fontWeight: 'bold' }}>Kreativnost</h4>
                    <div className='font-size-15'>
                        Zato uraditi neto na prost nain, kada moemo kreativno. Zavirite u nae uionice, vidite arenilo svugde, i panoe koje krase prava umetnika dela malih velikih ljudi, posluajte neki as ili zavirite u nau galeriju i naboj kreativnosti e vas odmah obuzeti!
                    </div>
                  </div>
                </div>
              </div>
              <div className='col-md-4'>
                <div className='row'>
                  <div className='col-5'>
                    <img src={Imagethree} alt='Rekreacija' className='w-100' />
                  </div>
                  <div className='col pl-0'>
                    <h4 style={{ fontWeight: 'bold' }}>Rekreacija</h4>
                    <div className='font-size-15'>
                        Deca su kinestetika bia, ona i ue o svetu oko sebe kreui se kroz njega. Nai aci nisu prikovani za stolice, pogleda uprtih u tablu, oni su svugde, a najee u igrama koje imaju za cilj da ih motiviu da novo znanje primene na njima svojstven nain.
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  );
};

export default Dashboard;
